import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Transport from "./TransportU2F.js";
import Btc from './Btc.js';

class App extends Component {
  constructor() {
    super();
    this.state = {btcAddress: ""};
  }
  render() {
    const getBtcAddress = async () => {
      const transport = await Transport.create();
      const btc = new Btc(transport);
      const result = await btc.getWalletPublicKey("44'/0'/0'/0");
      return result.bitcoinAddress;
    };
    getBtcAddress().then(a => console.log(this.setState({btcAddress:a})));
    /*tst
    const transport = await Transport.create();
    const btc = new Btc(transport);
    const result = btc.getWalletPublicKey("44'/0'/0'/0").then(
      success=>this.setState({btcAddress:"success"}));
      */
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Welcome to React</h1>
        </header>
        <p className="App-intro">
          {this.state.btcAddress}
        </p>
      </div>
    );
  }
}

export default App;



